.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 90%;
  top: 3rem;
  left: 5%;
  position: absolute;
  z-index: 5;
  cursor: default;
}

.logo,
.shop,
.btn,
.cartLengthBlack {
  color: var(--primary);
}

.logoBlack,
.shopBlack,
.btnBlack,
.cartLength {
  color: var(--shopPrimary);
}

.logo {
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Montserrat Medium";
  font-weight: 500;
  letter-spacing: 5px;
}

.logoBlack {
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Montserrat Medium";
  font-weight: 500;
  letter-spacing: 5px;
}

.nav {
  width: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.shop {
  text-decoration: none;
  font-size: 0.9rem;
  font-family: "Montserrat SemiBold";
  font-weight: 600;
}

.shopBlack {
  text-decoration: none;
  font-size: 0.9rem;
  font-family: "Montserrat SemiBold";
  font-weight: 600;
}

.btn {
  background: none;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  outline: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: "Montserrat SemiBold";
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.btnBlack {
  background: none;
  border: none;
  font-size: 0.9rem;
  cursor: pointer;
  outline: none;
  font-family: "Montserrat SemiBold";
}

.cartBtnContainer {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.cartLength {
  position: absolute;
  width: 29px;
  height: 29px;
  left: calc(-100% - 7px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 3px;
  background: var(--shopSecondary);
  font-size: 0.9rem;
  font-family: "Montserrat Bold";
  font-weight: 700;
}

.cartLengthBlack {
  position: absolute;
  width: 29px;
  height: 29px;
  left: calc(-100% - 7px);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-radius: 3px;
  background: var(--shopPrimary);
  font-size: 0.9rem;
  font-family: "Montserrat Bold";
  font-weight: 700;
}

.svgArrow {
  position: absolute;
  left: calc(100% - 1px);
  color: var(--shopSecondary);
}

.svgArrowBlack {
  position: absolute;
  left: calc(100% - 2px);
  color: var(--shopPrimary);
}

.svgCart {
  width: 29px;
  height: 29px;
}

@media (max-width: 780px) {
  .container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    height: 100px;
    top: 5%;
  }
  .nav {
    width: 100%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
  }
}
